import React, { useState, useEffect } from "react";
import queryString from 'query-string';
import io from "socket.io-client";
import TextContainer from '../TextContainer/TextContainer';
import Messages from '../Messages/Messages';
import InfoBar from '../InfoBar/InfoBar';
import Input from '../Input/Input';

import './Chat.css';
let socket;
const Chat = ({ location }) => {
    const [name, setName] = useState('');
    const [room, setRoom] = useState('');
    const [message, setMessage] = useState('');
    const [users, setUsers] = useState('');
    const [messages, setMessages] = useState([]);
    const ENDPOINT = 'https://react-chat-app2839.herokuapp.com/';
  
    useEffect(() => {
      const { name, room } = queryString.parse(location.search);
  
      socket = io(ENDPOINT);
  
      setRoom(room);
      setName(name)
  
      socket.emit('join', { name, room }, (error) => {
        if(error) {
          alert(error);
        }
      });
    }, [ENDPOINT, location.search]);
  
    useEffect(() => {
      socket.on('message', (message) => {
        setMessages([...messages, message ]);
      });
  
      socket.on('roomData', ({ users }) => {
        setUsers(users);
      })
  
      return () => {
        socket.emit('disconnect');
  
        socket.off();
      }
    }, [messages])
  
    const sendMessage = (event) => {
      event.preventDefault();
  
      if(message) {
        socket.emit('sendMessage', message, () => setMessage(''));
      }
    }
  
  
    return(
    <div className="d-flex justify-content-center d-flex align-items-center  outerContainer">
    <div className="d-flex flex-column  d-flex justify-content-between  container">
        <InfoBar room={room} />
        <Messages messages={messages} name={name} />
        <Input message={message} setMessage={setMessage} sendMessage={sendMessage} />
    </div>
    <TextContainer users={users}/>
  </div>)
}
  export default Chat;
